							<text class="btn-text">Áî≥ËØ∑Ë¥∑Ê¨æ</text>
						</view>
					</view>
				</view>

				<view class="empty-tip" v-if="pendingCustomerList.length === 0">
					ÊöÇÊó†ÂæÖÂ§ÑÁêÜÂÆ¢Êà∑Êï∞ÊçÆ
