			loanForm: {
				product: null,
				amount: '',
				term: null,
				purpose: null,
				monthlyIncome: ''
			},
			marriageOptions: ['Êú™Â©ö', 'Â∑≤Â©ö', 'Á¶ªÂºÇ', '‰∏ßÂÅ∂'],
			loanProducts: ['‰∏™‰∫∫Ê∂àË¥πË¥∑', 'ÁªèËê•Ë¥∑', 'ÊàøÂ±ãÊäµÊäºË¥∑', 'Ê±ΩËΩ¶ÊäµÊäºË¥∑', '‰ø°Áî®Ë¥∑'],
			loanTerms: ['3‰∏™Êúà', '6‰∏™Êúà', '12‰∏™Êúà', '24‰∏™Êúà', '36‰∏™Êúà', '48‰∏™Êúà', '60‰∏™Êúà'],
			loanPurposes: ['Ê∂àË¥π', 'ÁªèËê•', 'Ë£Ö‰øÆ', 'ÊïôËÇ≤', 'ÂåªÁñó', 'ÊóÖÊ∏∏', 'ÂÖ∂‰ªñ'],
			uploadList: [
				{
					label: 'Ë∫´‰ªΩËØÅÊ≠£Èù¢',
					path: ''
