					manager: 'ËµµÁªèÁêÜ',
					customerGroup: 'ÁªèËê•',
					status: 'ÂÆ°Êâπ‰∏≠',
					assignedBy: 'Áéã‰∫ßÂìÅ'
				},
				{
					id: 3,
					name: 'Áéã‰∫î',
