			];

			// Â∫îÁî®ÊêúÁ¥¢ËøáÊª§
			if (this.searchKey) {
				this.pendingCustomerList = this.pendingCustomerList.filter(item =>
					item.name.includes(this.searchKey) ||
					item.phone.includes(this.searchKey)
				);
			}
		},

		// ÊêúÁ¥¢ÂÆ¢Êà∑
		searchCustomers() {
			this.loadPendingCustomers();
		},

		// Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ª
		getStatusClass(status) {
			if (!status) return 'pending';

			const statusMap = {
				'ÂæÖÂÆ°Êâπ': 'pending',
				'ÂÆ°Êâπ‰∏≠': 'processing',
				'Â∑≤ÈÄöËøá': 'approved',
				'Â∑≤ÊãíÁªù': 'rejected'
			};

			return statusMap[status] || 'pending';
		},

		// Êü•ÁúãÂÆ¢Êà∑ËØ¶ÊÉÖ
		handleViewCustomer(item) {
			uni.navigateTo({
				url: `/pages/customer/detail?id=${item.id}&type=loan`
			});
		},

		// ‰∏∫ÂÆ¢Êà∑ÂàõÂª∫Ë¥∑Ê¨æ
		handleCreateLoan(item) {
			// Ë∑≥ËΩ¨Âà∞Ë¥∑Ê¨æÁî≥ËØ∑È°µÈù¢ÔºåÂπ∂‰º†ÈÄíÂÆ¢Êà∑ID
			uni.navigateTo({
				url: `/pages/loan/apply?id=${item.id}`
			});
		},

		marriageChange(e) {
			this.customerForm.marriage = e.detail.value;
		},
		productChange(e) {
			this.loanForm.product = e.detail.value;
		},
		termChange(e) {
			this.loanForm.term = e.detail.value;
		},
		purposeChange(e) {
			this.loanForm.purpose = e.detail.value;
		},
		chooseImage(index) {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.uploadList[index].path = res.tempFilePaths[0];
				}
			});
		},
		resetForm() {
			this.customerForm = {
				name: '',
				phone: '',
				idCard: '',
				age: '',
				marriage: null
			};
			this.loanForm = {
				product: null,
				amount: '',
				term: null,
				purpose: null,
				monthlyIncome: ''
			};
			this.uploadList.forEach(item => {
				item.path = '';
			});
		},
		validateForm() {
			// Ë°®ÂçïÈ™åËØÅÈÄªËæë
			if (!this.customerForm.name) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÂÆ¢Êà∑ÂßìÂêç',
					icon: 'none'
